<template lang="html">
  <div class="">
    <default-head header="添加好友"></default-head>
    <div class="" id="wrapper">
      <info-line pathname="/searchcontact" class="info-line-text" width=".46rem" height=".46rem" :icon="icons[0]" >
        输入号召力账号
      </info-line>
      <info-line v-if="false" class="info-line-text" width=".46rem" height=".46rem" :icon="icons[1]" >
        扫一扫添加好友
      </info-line>
      <info-line v-if="false" class="info-line-text" width=".46rem" height=".46rem" :icon="icons[2]" >
        添加手机联系人
      </info-line>
      <p class="my-ewm" v-if="false">我的二维码名片<i class="ewm"></i></p>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import infoLine from '@/components/common/infoLine'
import search from '@/assets/img/addfriend/search01@3x.png'
import sao from '@/assets/img/addfriend/i02@3x.png'
import people from '@/assets/img/addfriend/i03@3x.png'
export default {
  components: {defaultHead, infoLine},
  data () {
    return {
      icons: [search, sao, people]
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.info-line-text {
  color: @midleColor;
  font-size: .32rem;
}
.my-ewm {
  font-size: .32rem;
  line-height: .32rem;
  padding-top: .4rem;
  text-align: center;
  .ewm {
    display: inline-block;
    width: .3rem;
    height: .3rem;
    position: relative;
    top: .04rem;
    left: .1rem;
    background: tan;
  }
}
</style>



